.StudentList {
    width: 100%;
    font-family: Poppins;
}

.StudentList .upper {
    width: 100%;
    height: max-content;
    border-radius: 15px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    padding: 20px;
    margin-bottom: 20px;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.StudentList .upper h3 {
    font-weight: bold;
    margin-bottom: 0;
}

.StudentList .upper .Search .searchDiv {
    background-color: #f2f4f8;
    padding: 5px 10px;
    border-radius: 10px;
    display: flex;
    align-items: center;
}

.StudentList .upper .Search .searchDiv .searchBar {
    border: 0;
    outline: none;
    background: none;
    border-left: 1px solid #b7c1ae;
    padding-left: 10px;
}

.StudentList .upper .Search .searchDiv img:last-child {
    cursor: pointer;
}

.StudentList .StdList {
    width: 100%;
    border-radius: 15px;
    padding: 20px;
    margin-bottom: 20px;
    background-color: #fff;
    overflow-y: auto;
}

.StudentList .StdList .table tbody tr {
    position: relative;
}

.StudentList .StdList .table tbody td:last-child {
    position: absolute;
    top: 0;
    right: 0;
}

.StudentList .StdList .table tbody td:last-child img {
    cursor: pointer;
    opacity: 0;
    transition: .5s;
}

.StudentList .StdList .table tbody td:last-child img:first-child:hover:after {
    content: "Edit";
}

.StudentList .StdList .table tbody tr:hover > td img {
    opacity: 1;
}

@media screen and (max-width: 600px) {
    .StudentList .upper {
        display: block;
    }
    .StudentList .upper h3 {
        margin-bottom: 10px;
    }
    .StudentList .upper .Search .searchDiv {
        padding: 12px;
    }
}